@keyframes left-animation{
  0%    {
    left: -10px
  }

  10%   {
    left: -8px
  }

  20%   {
    left: -6px
  }

  30%   {
    left: -4px
  }

  40%   {
    left: -2px
  }

  50%   {
    left: 0
  }

  60%   {
    left: -2px
  }

  70%   {
    left: -4px
  }

  80%   {
    left: -6px
  }

  90%   {
    left: -8px
  }

  100%  {
    left: -10px
  }
}

@keyframes right-animation{
  0%    {
    left: 10px
  }

  10%   {
    left: 8px
  }

  20%   {
    left: 6px
  }

  30%   {
    left: 4px
  }

  40%   {
    left: 2px
  }

  50%   {
    left: 0
  }

  60%   {
    left: 2px
  }

  70%   {
    left: 4px
  }

  80%   {
    left: 6px
  }

  90%   {
    left: 8px
  }

  100%  {
    left: 10px
  }
}

.lt{
  position:relative;
  animation-name: left-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

.gt{
  position:relative;
  animation-name: right-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

